h1,
p {
  font-family: Lato;
}
h1 {
  text-align: center;
}

h2 {
  border-bottom: 1px solid #099;
  width: 100%;
}
html,
body {
  max-height: 100vh;
  overflow: hidden;
}
body {
  color: #ddd;
  background: #888;
}
span[lang] {
  display: none;
}
#main[lang='ENG'] span[lang='ENG'] {
  display: inline-block;
}
#main[lang='CHT'] span[lang='CHT'] {
  display: inline-block;
}
#main {
  border-radius: 10px;
  background: #333;
  height: 90vh;
  display: block;
  width: 90vw;
  margin: 3vh 3vw;
  padding: 2vh 2vw;
}
#navigateDiv {
  float: left;
  height: 100%;
  width: 100px;
  margin-right: 50px;
  margin-top: 20px;
}

.content {
  margin-left: 20px;
  float: left;
  width: 80%;
  display: none;
}
.content.show {
  display: block;
}

.headerButton {
  cursor: pointer;
  border-bottom: 1px solid #099;
  transition: all 0.2s;
  float: left;
  width: 100%;
  padding: 10px;
  margin-bottom: 5px;
  &.active {
    color: #a50;
    border-bottom: 1px solid #a50;
    padding-left: 15px;
    padding-right: 5px;
  }
  &:hover {
    color: #d00;
    border-bottom: 1px solid #d00;
    padding-left: 20px;
    padding-right: 0px;
  }
}


a:link {
  color: #AAAAEE;
}
a:visited {
  color: #995BEC;
}